@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.5.0/font/bootstrap-icons.css);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans:wght@300,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f1f1f1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.filter{font-family:'Lato', sans-serif;font-size:small;cursor:pointer;font-weight:bold;color:#847b71}.filter .elements{padding-top:5px;padding-bottom:5px;padding-left:5px}.filter .label-hover:hover{width:100%;color:black;background-color:white}

.title p{font-size:16px;font-family:'Lato', sans-serif;font-weight:bold}

.storyCard{line-height:normal;background-color:#fff;box-shadow:#cec3c3 1px 1px 5px 1px;padding:10px}.storyCard .head .profileCard{display:flex;align-items:center;cursor:pointer}.storyCard .head .profileCard img{width:35px;height:35px;margin:10px;border-radius:50%}.storyCard .body img{width:100%}.storyCard .footer .icon{cursor:pointer}

.smallTitleCard{display:flex;align-items:center;margin:10px 0}.smallTitleCard img{width:50px;border-radius:10%;margin:0 10px}.smallTitleCard h6{margin:0 10px}

.main{display:flex;justify-content:space-between;margin-top:13vh;background-color:#f1f1f1}.main .left{width:20vw;background-color:#f1f1f1}.main .left .overflowscroll{height:72vh;overflow-y:scroll}.main .left ::-webkit-scrollbar{width:5px}.main .left ::-webkit-scrollbar-track{background:#f1f1f1;border-radius:2px}.main .left ::-webkit-scrollbar-thumb{background:#cbc6c1;border-radius:2px}.main .left ::-webkit-scrollbar-thumb:hover{background:#cbc6c1}.main .middle{width:50vw;height:80vh;overflow-y:scroll}.main .middle ::-webkit-scrollbar{width:5px}.main .middle ::-webkit-scrollbar-track{background:#f1f1f1;border-radius:2px}.main .middle ::-webkit-scrollbar-thumb{background:#cbc6c1;border-radius:2px}.main .middle ::-webkit-scrollbar-thumb:hover{background:#cbc6c1}.main .right{width:25vw;height:80vh}.main .active{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;border-bottom:4px dotted #b57e40}

.mt-15{margin-top:10vh}.sign-in{font-family:'Merriweather Sans', sans-serif}.sign-in .login-card{margin-top:20px;width:45%;height:85vh;background:#fafaf9;box-shadow:2px 2px 12px rgba(35,31,26,0.08)}.sign-in .login-contain{display:flex;align-items:center;justify-content:center}.sign-in .sign-form{width:80%;margin-left:auto;margin-right:auto}.sign-in .login-forget{text-decoration:none;color:#b57e40;font-size:13px;font-weight:bold}.sign-in .login-forget:hover{text-decoration:underline;color:#8a5c28}.sign-in .login-field:focus{box-shadow:none;border:2px solid #b57e40}.sign-in .form-text{font-size:12px}.sign-in .btn-signin-main{background-color:#b57e40}.sign-in .btn-signin-main:hover{background-color:#8a5c28}.sign-in .btn-signin{background-color:#faf5f0;box-shadow:2px 2px 12px rgba(35,31,26,0.08)}.sign-in .btn-signin:hover{background-color:white}.sign-in .icon{color:#b57e40}

.sign-up{font-family:'Merriweather Sans', sans-serif}.sign-up .login-card{margin-top:20px;width:45%;background:#fafaf9;box-shadow:2px 2px 12px rgba(35,31,26,0.08)}.sign-up .login-contain{display:flex;align-items:center;justify-content:center}.sign-up .sign-form{width:80%;margin-left:auto;margin-right:auto}.sign-up .form-text{font-size:12px}.sign-up .btn-signin-main{background-color:#b57e40}.sign-up .btn-signin-main:hover{background-color:#8a5c28}.sign-up .login-forget{text-decoration:none;color:#b57e40;font-size:13px;font-weight:bold}.sign-up .login-forget:hover{text-decoration:underline;color:#8a5c28}.sign-up .agree-font{font-size:13px}.sign-up .btn-signin{background-color:#faf5f0;box-shadow:2px 2px 12px rgba(35,31,26,0.08)}.sign-up .btn-signin:hover{background-color:white}.sign-up .icon{color:#b57e40}

.header{font-family:'Lato', sans-serif;display:flex;flex-wrap:wrap;flex-direction:row;justify-content:space-evenly;align-items:center;padding:10px;background-color:#f1f1f1}.header .icon{font-size:20px;margin:0 2px 2px 2px}.header .content-left{display:flex;width:33%}.header .content-left .link{padding:0 15px}.header .content-left .link:hover{background-color:#ffffff;border-radius:2px}.header .content-middle{height:auto;width:33%}.header .content-middle .has-search{position:relative;top:10px;left:30px}.header .content-right{display:flex;justify-content:space-evenly;align-items:center;width:33%}.header .content-right .primary{font-weight:bold;background-color:#b57e40;color:#ffffff}.header .content-right .primary:hover{background-color:#b68341}.header .content-right .secondary{font-weight:bold;background-color:#fdecdb;color:#b68341}.header .content-right .secondary:hover{background-color:#ffffff}.header .content-right .btn-border{border-radius:5px}.header .content-right .profileLogo{border:none;cursor:pointer;width:40px;height:40px;font-weight:bold;text-align:center;vertical-align:middle;font-size:22px;color:#ffffff;border-radius:50%;background-color:#b57e40}.header .content-right .profileLogo .subMenuItems{width:200px;position:relative;right:150px;top:10px;padding:15px 15px 1px 10px;background-color:#ffffff;color:black;line-height:normal;font-size:16px;font-weight:700;text-align:left;display:none}.header .content-right .profileLogo .subMenuItems p{padding:4px}.header .content-right .profileLogo .subMenuItems p:hover{background-color:#f8f4f4}.header .content-right .profileLogo:focus{text-decoration:none}.header .content-right .profileLogo:focus .subMenuItems{display:block}.header .link{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;display:flex;justify-content:space-evenly;align-items:center;color:#847B71;text-decoration:none;margin:10px 20px}.header .active{color:#b57e40}

.addstory{width:60%;margin:auto;font-family:'Merriweather Sans', sans-serif}.addstory ::-webkit-input-placeholder{font-family:'Merriweather Sans', sans-serif;font-weight:bold;font-size:25px}.addstory ::-webkit-textarea-placeholder{font-family:'Merriweather Sans', sans-serif;font-weight:bold;font-size:25px}.addstory .story-content{border:0px;box-shadow:none}.addstory .story-title{border:0px;box-shadow:none}.addstory .categories:focus{box-shadow:none;border:1px solid #b57e40}.addstory .categories:hover{border:1px solid #b57e40}.addstory .mt-15{margin-top:20vh}.addstory .publish-btn{background-color:#b57e40;box-shadow:2px 2px 12px rgba(35,31,26,0.08);font-weight:bold;border:none;width:10%;float:right;color:#ffffff}.addstory .publish-btn:hover{background-color:#f5d1a9;color:#b57e40}.addstory .label-text{font-family:'Lato', sans-serif;font-weight:bold}

.profile{width:50vw;margin-top:13.2vh;background-color:#f1f1f1}.profile .head{display:flex;align-items:center}.profile .head img{width:150px}.profile .middle{width:50vw}.profile .active{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;border-bottom:4px dotted #b57e40}

.pointer{cursor:pointer}

